:root {
  --font-primary: "Inter", sans-serif;
  --font-secondary: "Lexend Deca", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --verydarkblue: hsl(233, 47%, 7%);
  --darkdesaturatedblue: hsl(244, 38%, 16%);
  --softviolet: hsl(277, 64%, 61%);
  --slightlytransparentwhite: hsla(0, 0%, 100%, 0.75);
  --transparentwhite: hsla(0, 0%, 100%, 0.6);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 15px;
  font-family: var(--font-primary);
  background-color: var(--verydarkblue);
  height: 95vh;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 0;
  max-width: 90rem;
  height: 100vh;
}

footer {
  position: fixed;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}
footer .attribution {
  font-size: 11px;
  text-align: center;
  color: var(--transparentwhite);
}
footer .attribution a {
  color: var(--softviolet);
}

.wrapper {
  display: grid;
  grid-template-columns: 570px 540px;
  grid-template-rows: 446px;
  justify-content: center;
  align-content: center;
  grid-template-areas: "details picture";
}
@media (max-width: 71.875em) {
  .wrapper {
    grid-template-columns: minmax(416px, 738px);
    grid-template-rows: 380px 400px;
    grid-template-areas: "picture" "details";
    margin: 3rem auto;
  }
}
@media (max-width: 32.5em) {
  .wrapper {
    grid-template-columns: 326px;
    grid-template-rows: 240px 540px;
  }
}

.details {
  grid-area: details;
  padding: 5rem 4.375rem;
  background-color: var(--darkdesaturatedblue);
  border-radius: 10px 0 0 10px;
}
@media (max-width: 71.875em) {
  .details {
    padding-top: 3rem;
    border-radius: 0 0 10px 10px;
  }
}
@media (max-width: 32.5em) {
  .details {
    border-radius: 0 0 10px 10px;
    padding: 2rem;
    text-align: center;
  }
}
.details h1 {
  color: var(--white);
  font-size: 2.5rem;
  line-height: 1.2;
}
@media (max-width: 32.5em) {
  .details h1 {
    font-size: 1.7rem;
  }
}
.details h1 span {
  color: var(--softviolet);
}
.details h2 {
  color: var(--white);
}
.details p.main {
  color: var(--transparentwhite);
  margin-top: 2rem;
  line-height: 1.7;
  padding-right: 5rem;
}
@media (max-width: 32.5em) {
  .details p.main {
    padding: 0;
    margin-top: 1rem;
  }
}
.details .stats {
  display: grid;
  margin-top: 4rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  gap: 2px;
  height: 54px;
  grid-template-areas: "stat1 stat2 stat3" "title1 title2 title3";
}
.details .stats .stat1 {
  grid-area: stat1;
}
.details .stats .stat2 {
  grid-area: stat2;
}
.details .stats .stat3 {
  grid-area: stat3;
}
.details .stats .title1 {
  grid-area: title1;
}
.details .stats .title2 {
  grid-area: title2;
}
.details .stats .title3 {
  grid-area: title3;
}
.details .stats h2 {
  color: var(--white);
  font-size: 1.5rem;
}
.details .stats p {
  padding: 0;
  margin: 0;
  font-family: var(--font-secondary);
  letter-spacing: 1px;
  text-transform: uppercase;
  color: var(--transparentwhite);
  font-size: 0.75rem;
}
@media (max-width: 71.875em) {
  .details .stats {
    margin-left: 5rem;
  }
}
@media (max-width: 32.5em) {
  .details .stats {
    margin-left: 0;
    grid-template-columns: 100px;
    grid-template-rows: repeat(6, 35px);
    gap: 0;
    grid-template-areas: "stat1" "title1" "stat2" "title2" "stat3" "title3";
    margin-top: 2rem;
    height: 100px;
  }
}

.picture {
  overflow: hidden;
  grid-area: picture;
  border-radius: 0 10px 10px 0;
  position: relative;
  background-image: url(../app/images/image-header-desktop.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: aquamarine;
  background-position: center center;
}
@media (max-width: 71.875em) {
  .picture {
    border-radius: 10px 10px 0 0;
  }
}
.picture .layer-violet {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(115, 5, 184, 0.541);
}